* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

$main-color: #69510F;


.image {
    position: relative;
    height: 100vh;
    max-width: 100%;
    background-image: url(../svg/firstPagePhotoSvg.svg);
    background-size: cover;
    background-position: center;

    .menuContainer {
        height: 80vh;
        width: 20%;
        position: absolute;
        display: grid;
        grid-template-rows: 1fr 1fr 1fr 1fr;
        grid-template-columns: 1fr;
        text-align: center;
        align-items: center;
        color: whitesmoke;
        top: 10%;
    
    
        .menuElement {
            transform: rotate(270deg);
            font-size: 0.9rem;
            font-family: 'Rokkitt', serif;
            font-weight: 400;
            cursor: pointer;
        }
    }

    .titleBoxer {
        position: absolute;
        bottom: 15%;
        left: 60%;
        transform: translate(-50%, -50%);
        text-align: center;

        .title {
            color: white;
            font-size: 1.6rem;
            font-family: 'Bitter', serif;
            font-weight: 400;
        }
    
        .subtitle {
            color: $main-color;
            font-family: 'Bitter', serif;
            font-size: 1rem;
            font-weight: 400;

            display: flex;
            width: 100%;
            justify-content: center;
            align-items: center;
            text-align: center;
        }

        
        .subtitle:after, .subtitle:before {
            // content: '';
            // display: block;
            // position: absolute;
            // align-items: center;
            // background-color: $main-color;
            // width: 50px;
            // height: 3px;

            content: '';
            border-top: 1px solid;
            margin: 0 5% 0 5%;
            width: 20px;
        }
    }

    .arrowDown {
        height: 26px;
        position: absolute;
        left: 50%;
        bottom: 0;
        transform: translate(-50%, -50%);
        cursor: pointer;
    }
}

//                                            400px

@media (min-width: 400px) {

    .image {

        .menuContainer {

            .menuElement {
                font-size: 1.1rem;
            }
        }
    }

    .titleBoxer {

        .title {
            font-size: 1.8rem !important;
        }
    }

    .arrowDown {
        height: 32px !important;
    }
}