* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

$main-color: #69510F;

.seventhPage {
    position: relative;
    height: 100vh;
    width: 100%;
    background-color: black;

    .galleryTitle {
        position: relative;
        height: 12.5%;
        // width: 100%;

        .galleryWrapper {
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);


            .gallerySubtitle {
                color: $main-color;
                font-size: 0.9rem;
                font-family: 'Rokkitt', serif;
                font-weight: 400;
                text-align: center;
            }
    
            .galleryTitle {
                color: whitesmoke;
                display: flex;
                font-size: 1.2rem;
                font-family: 'Rokkitt', serif;
                font-weight: 400;
                text-align: center;
            }
        }
    }

    .starterContainer {
        position: relative;
        height: 25%;
        width: 100%;

        .starter {
            height: 100%;
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
        }

        .starterTitle {
            color: $main-color;
            font-size: 1rem;
            font-family: 'Rokkitt', serif;
            font-weight: 400;

            position: absolute;
            bottom: 0%;
            left: 50%;
            transform: translate(-50%, -50%);
        }
    }

    .mainCourseContainer {
        position: relative;
        height: 25%;
        width: 100%;

        .mainCourse {
            height: 75%;
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
        }

        .mainCourseTitle {
            color: $main-color;
            font-size: 1rem;
            font-family: 'Rokkitt', serif;
            font-weight: 400;

            position: absolute;
            bottom: 5%;
            left: 50%;
            transform: translate(-50%, -50%);
        }
    }

    .coctailContainer {
        position: relative;
        height: 25%;
        width: 100%;

        .coctail {
            height: 80%;
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
        }

        .coctailTitle {
            color: $main-color;
            font-size: 1rem;
            font-family: 'Rokkitt', serif;
            font-weight: 400;

            position: absolute;
            bottom: -10%;
            left: 50%;
            transform: translate(-50%, -50%);
        }
    }

    .arrowRightGallery {
        height: 40px;
        position: absolute;
        top: 50%;
        right: 0;
        transform: translate(-50%, -50%);
    }

    .arrowDownGallery {
        height: 12.5%;
        width: 100%;
    }

    .arrowDownSix {
        height: 26px;
        position: absolute;
        bottom: 0%;
        left: 50%;
        transform: translate(-50%, -50%);
        cursor: pointer;
    }
}

//                                            400px

@media (min-width: 400px) and (orientation: portrait) {
    .galleryTitle {
        .gallerySubtitle {
            font-size: 1rem !important;
        }

        .galleryTitle {
            font-size: 1.4rem !important;
        }
    }

    .starterContainer {
        .starterTitle {
            font-size: 1.1rem !important;
        }
    }

    .mainCourseContainer {
        .mainCourseTitle {
            font-size: 1.1rem !important;
        }
    }

    .coctailContainer {
        .coctailTitle {
            font-size: 1.1rem !important;
        }
    }

    .arrowDownSix {
        height: 32px !important;
    }
}