* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

$main-color: #69510F;

.contactPage {
    height: 30vh;
    width: 100%;
    background-color: black;

    .contactBox {
        position: relative;
        height: 70%;

        .contactTitle {
            color: whitesmoke;
            text-align: center;
            font-size: 0.7rem;
            font-family: 'Rokkitt', serif;
            font-weight: 400;

            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
        }
    }

    .mediaBox {
        position: relative;
        height: 30%;
        display: grid;
        grid-template-columns: 1fr 1fr 1fr;
        grid-template-rows: 1fr;
        text-align: center;

        .facebookBox {
            position: relative;

            .facebook {
                color: whitesmoke;
                font-size: 0.8rem;
                font-family: 'Rokkitt', serif;
                font-weight: 400;
    
                position: absolute;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%);
                width: 100%;
            }

            .facebook::before {
                content: '';
                position: absolute;
                width: 35%;
                height: 1px;
                background-color: $main-color;
                bottom: 0;
                left: 15%;
            }
        }

        .instagramBox {
            position: relative;

            .instagram {
                color: whitesmoke;
                font-size: 0.8rem;
                font-family: 'Rokkitt', serif;
                font-weight: 400;

                position: absolute;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%);
                width: 100%;
            }

            .instagram::before {
                content: '';
                position: absolute;
                width: 35%;
                height: 1px;
                background-color: $main-color;
                top: 0;
                left: 15%;
            }
        }

        .tripAdvisorBox {
            position: relative;

            .tripAdvisor {
                color: whitesmoke;
                font-size: 0.8rem;
                font-family: 'Rokkitt', serif;
                font-weight: 400;
    
                position: absolute;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%);
                width: 100%;
            }

            .tripAdvisor::before {
                content: '';
                position: absolute;
                width: 35%;
                height: 1px;
                background-color: $main-color;
                bottom: 0;
                left: 10%;
            }
        }
    }
}

@media (min-width: 400px) and (orientation: portrait) {
    .contactPage {
        .contactBox {
            .contactTitle {
                font-size: 0.8rem;;
            }
        }

        
        .facebookBox {
            .facebook {
                font-size: 1rem !important;
            }

            .facebook::before {
                width: 30%;
            }
        }

        .instagramBox {
            .instagram {
                font-size: 1rem !important;
            }

            .instagram::before {
                width: 30%;
            }
        }

        .tripAdvisorBox {
            .tripAdvisor {
                font-size: 1rem !important;
            }

            .tripAdvisor::before {
                width: 30%;
            }
        }
    }
}