* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

$main-color: #69510F;

.thirdPage {
    height: 100vh;
    width: 100%;
    background-color: black;
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 1fr 1fr;

    .containerOne {
        position: relative;

        .containerInsideOne {
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);


            img {
                height: 64px;
                display: block;
                margin: 0 auto;
            }
    
            h1 {
                text-align: center;
                color: $main-color;
                font-size: 1rem;
                padding: 5%;
            }
    
            p {
                color: whitesmoke;
                text-align: center;
                font-size: 0.7rem;
            }
        }
    }

    .containerTwo {
        position: relative;

        .containerInsideTwo {
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);


            img {
                height: 64px;
                display: block;
                margin: 0 auto;
            }
    
            h1 {
                text-align: center;
                color: $main-color;
                font-size: 1rem;
                padding: 5%;
            }
    
            p {
                color: whitesmoke;
                text-align: center;
                font-size: 0.7rem;
            }
        }
    }
}

//                                           400px

@media (min-width: 400px) {
    .thirdPage {
        .containerOne{
            .containerInsideOne {
                img {
                    height: 80px;
                }

                h1 {
                    font-size: 1.2rem;
                    font-weight: 400;
                }

                p {
                    font-size: 0.8rem;
                }
            }
        }

        .containerTwo{
            .containerInsideTwo {
                img {
                    height: 80px;
                }

                h1 {
                    font-size: 1.2rem;
                    font-weight: 400;
                }

                p {
                    font-size: 0.8rem;
                }
            }
        }
    }
}