* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

$main-color: #69510F;

.fivePage {
    height: 100vh;
    width: 100%;
    background-color: black;
    display: grid;
    grid-template-rows: 1fr 1fr;
    grid-template-columns: 1fr;

    .reviewPhoto {
        background-image: url(../svg/reviewPhotoSvg.svg);
        background-size: cover;
        background-position: center;
    }

    .review {
        position: relative;

        .titleBox {
            position: relative;
            width: 100%;
            height: 5vh;

            .title {
                position: absolute;
                color: whitesmoke;
                text-align: center;
                font-size: 1rem;
                letter-spacing: 3px;
                left: 50%;
                top: 50%;
                transform: translate(-50%, -50%);
            }

            .title::after {
                content: '';
                display: block;
                width: 30%;
                height: 1px;
                background-color: $main-color;
                margin: 0 auto;
            }
        }

        //                                                             SUBTITLE

        .subtitleBox {
            position: relative;
            width: 100%;
            height: 5vh;

            .subtitle {
                position: absolute;
                color: whitesmoke;
                text-align: center;
                font-size: 0.75rem;
                left: 50%;
                top: 50%;
                transform: translate(-50%, -50%);
            }
        }

        .nonActive {
            opacity: 0;
        }

        .commasBox {
            position: relative;
            width: 100%;
            height: 5vh;

            .commas {
                position: absolute;
                color: $main-color;
                text-align: center;
                left: 50%;
                top: 20%;
                transform: translate(-50%, -50%);
            }
        }

        //                                                             OPINION

        .opinionBox {
            position: relative;
            width: 100%;
            height: 20vh;

            
            .opinion {
                position: absolute;
                color: whitesmoke;
                text-align: center;
                font-size: 0.6rem;
                width: 50%;
                left: 50%;
                top: 50%;
                transform: translate(-50%, -50%);
            }
        }

        //                                                              NAME

        .nameBox {
            position: relative;
            width: 100%;
            height: 5vh;
            
            .name {
                position: absolute;
                color: $main-color;
                text-align: center;
                // font-size: calc(14px + (26 - 14) * ((100vw - 300px) / (1600 - 300)));
                font-size: 0.8rem;
                left: 50%;
                top: 50%;
                transform: translate(-50%, -50%);
            }
        }

        .arrowBox {
            position: relative;
            width: 100%;
            height: 10vh;

            .arrowDownFour {
                height: 26px;
                position: absolute;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%);
                cursor: pointer;
            }
        }

        .arrowRight {
            height: 26px;
            position: absolute;
            right: 0;
            bottom: 50%;
            transform: translate(-50%, -50%);
            cursor: pointer;
        }

        .arrowLeft {
            height: 26px;
            position: absolute;
            left: 7.5%;
            bottom: 50%;
            transform: translate(-50%, -50%) rotate(180deg);
            cursor: pointer;
        }
    }
}

//                                                     400px

@media (min-width: 400px) and (orientation: portrait) {
    .review {
        .titleBox {
            height: 10vh !important;

            .title{
                // font-size: calc(16px + (48 - 16) * ((100vw - 300px) / (1920 - 400))) !important;
                // font-family: 'Rokkitt', serif;
                // font-weight: 400;
                font-size: 1.4rem !important;
            }
        }

        .subtitleBox {
            .subtitle {
                // font-size: calc(14px + (36 - 14) * ((100vw - 300px) / (1920 - 400))) !important;
                // font-family: 'Rokkitt', serif;
                // font-weight: 400;
                font-size: 1rem !important;
            }
        }

        .commasBox {
            .commas {
                // font-size: calc(48px + (100 - 48) * ((100vw - 300px) / (1920 - 300))) !important;
                // font-family: 'Rokkitt', serif;
                // font-weight: 400;
                font-size: 2.6rem !important;
            }
        }

        .opinionBox {
            .opinion {
                // font-size: calc(10px + (24 - 10) * ((100vw - 300px) / (1920 - 300))) !important;
                // font-family: 'Rokkitt', serif;
                // font-weight: 400;
                font-size: 0.8rem !important;
                width: 62.5% !important;
            }
        }

        .nameBox {
            .name {
                // font-size: calc(14px + (36 - 14) * ((100vw - 300px) / (1920 - 300))) !important;
                // font-family: 'Rokkitt', serif;
                // font-weight: 400;
                font-size: 1rem !important;
            }
        }

        .arrowRight {
            height: 36px !important;
        }

        .arrowLeft {
            height: 36px !important;
        }







        .arrowBox {
            height: 5vh !important;

            .arrowDownFour {
                height: 32px;
            }
        }
    }
}